@import url('https://fonts.googleapis.com/css?family=Droid+Sans+Mono');

//$easeIn: cubic-bezier(0.25, 0.46, 0.45, 0.94)
//$easeOut: cubic-bezier(0.55, 0.085, 0.68, 0.53)
$easeInOut: cubic-bezier(0.455, 0.03, 0.515, 0.955);

$turnDuration: 0.6s;


$border: #333;
$card: #333;
$font-family: 'Droid Sans Mono', monospace;
$font-size: 20vw;
$font-width: lighter;
$font-color: lighten(white, 20%);


// 		--------------------------------		--------------------------------		---------
// 		--------------------------------		--------------------------------		---------

// Animation mixin
@mixin animate($name, $easing, $duration: 300ms, $delay: 0s, $iteration: 1, $direction: normal, $fill-mode: forwards) {
	animation: $name $duration $easing $delay $iteration $direction $fill-mode;
}

@keyframes fold {
	0% {
		transform: rotateX(0deg);
	}
	100% {
		transform: rotateX(-180deg);
	}
}

@keyframes unfold {
	0% {
		transform: rotateX(180deg);
	}
	100% {
		transform: rotateX(0deg);
	}
}

@font-face {
    font-family: "flip-font";
    src: local("../../src/static/fonts/gluqlo.ttf");
}


.flipUnitContainer {
    display: block;
    position: relative;
    width: 40vw;
    aspect-ratio: 1;
    perspective-origin: 50% 50%;
    /*perspective: 20%; */
    background-color: $card;
    border-radius: 10%;
    box-shadow: 0px 10px 10px -10px grey;

    .ContainerTitle {
        position: absolute;
        text-align: center;
        width: 100%;
        top: -15px;
        font-size: 10px;
        text-transform: uppercase;
    }


    .upperCard, .lowerCard {
        display: flex;
        position: relative;
        justify-content: center;
        width: 100%;
        height: 50%;
        overflow: hidden;
        /* border: 1px solid $border;*/ 
        
        span {
            font-size: $font-size;
            font-family: "flip-font"; /* $font-family; */
            font-family: $font-family;
            font-weight: $font-width;
            color: $font-color;
        }
    }

    .upperCard {
        align-items: flex-end;
        border-bottom: 2% solid $border;
        border-top-left-radius: 10%;
        border-top-right-radius: 10%;
        
        span {
            transform: translateY(50%);
        }
    }

    .lowerCard {
        align-items: flex-start;
        border-top: 2% solid $border;
        border-bottom-left-radius: 20%;
        border-bottom-right-radius: 20%;
        
        span {
            transform: translateY(-50%);
        }
    }

    .flipCard {
        display: flex;
        justify-content: center;
        position: absolute;
        left: 0;
        width: 100%;
    
        height: 50%;
        overflow: hidden;
        backface-visibility: hidden;
        
        span {
            font-family: $font-family;
            font-size: $font-size;
            font-weight: $font-width;
            color: $font-color;
        }
        
        &.unfold {
            top: 50%;
            align-items: flex-start;
            transform-origin: 50% 0%;
            transform: rotateX(180deg); // from 180 to 0
            background-color: $card;
            border-bottom-left-radius: 20%;
            border-bottom-right-radius: 20%;
            /* border: 1px solid $border; */
            border-top: 2% solid $border;

            span {
                transform: translateY(-50%);
            }

            @include animate(unfold, $easeInOut, 0.6s);
            transform-style: preserve-3d;
        }
            
        &.fold {
            top: 0%;
            align-items: flex-end;
            transform-origin: 50% 100%;
            transform: rotateX(0deg); // from 0 to -180
            background-color: $card;
            border-top-left-radius: 20%;
            border-top-right-radius: 20%;
            /* border: 0.5px solid $border; */
            border-bottom: 2% solid $border;
            
            span {
                transform: translateY(50%);	
            }

            @include animate(fold, $easeInOut, 0.6s);
            transform-style: preserve-3d;
        }
    }

}

.divider {
    font-size: 20vw;
    margin: auto 3px;
}